import { Component, Input, Output, EventEmitter } from "@angular/core";

@Component({
  selector: "lib-text",
  template: `<input
[id] ="id"
[name]="name"
[value]="value"
[tabindex]="tabindex"
[placeholder]="placeholder"
[disabled]="disabled"
[type]="type"
[maxLength]="maxLength"
[className]="className"
(focus)="onFocus($event)"
(blur)="onBlur($event)"
(input)="onChange($event)"
(keydown)="onKeyDown($event)"
(keypress)="onKeyPress($event)"
(keyup)="onKeyUp($event)"
/>`,
  styles: []
})
export class TextComponent {
  @Input() maxLength: Number = 10;
  @Input() isnumberonly: Boolean = false;
  @Input() className: object = {};
  @Input() regex: any;
  @Input() disabled: boolean = false;
  @Input() value: String;
  @Input() tabindex: Number = 0;
  @Input() type: String = "text";
  @Input() id: String = "";
  @Input() name: String = "";
  @Input() placeholder: String = "Enter value";

  @Output() change: EventEmitter<any> = new EventEmitter<any>();
  @Output() focus: EventEmitter<any> = new EventEmitter<any>();
  @Output() KeyDown: EventEmitter<any> = new EventEmitter<any>();
  @Output() KeyPress: EventEmitter<any> = new EventEmitter<any>();
  @Output() KeyUp: EventEmitter<any> = new EventEmitter<any>();

  @Output() blur: EventEmitter<any> = new EventEmitter<any>();

  onFocus(event) {
    this.focus.emit(event);
  }
  onBlur(event) {
    this.blur.emit(event);
  }
  onChange(event) {
    this.change.emit(event);
  }
  onKeyDown(event) {
    this.KeyDown.emit(event);
  }
  onKeyUp(event) {
    this.KeyUp.emit(event);
  }
  onKeyPress(event) {
    this.KeyPress.emit(event);
  }
}
